import React from "react";
import { Helmet } from "react-helmet";
import { useLocation } from "@reach/router";
import { graphql, StaticQuery } from "gatsby";

const SEO = ({ title, seoTitle, description, imageUrl, faqs }) => {
  let script;
  if (faqs) {
    const mainEntities = faqs.map(function (faq) {
      const items = Object.keys(faq).map((key) => {
        return faq[key];
      });

      return `{
        "@type": "Question",
        "name": "${items[0]}",
        "acceptedAnswer": {
          "@type": "Answer",
          "text": "${items[1]}"
        }
      }`;
    });

    script = `{
    "@context": "https://schema.org",
    "@type": "FAQPage",
    "mainEntity":[${mainEntities}]
  }`;
  }

  let { pathname } = useLocation();
  pathname = pathname.endsWith("/") ? pathname : pathname + "/"
  return (
    <StaticQuery
      query={graphql`
        query HelmetQuery {
          site {
            siteMetadata {
              title
              url
            }
          }
        }
      `}
      render={(data) => (
        <Helmet
          title={
            seoTitle || title || data.site.siteMetadata.title
          }
        >
          {description && <meta name="description" content={description} />}
          <meta name="og:url" content={`${data.site.siteMetadata.url}${pathname}`} />
          <meta name="og:title" content={seoTitle || title || data.site.siteMetadata.title} />
          <meta name="og:site_name" content="رایما" />
          {imageUrl && (
            <meta name="og:image" content={`${data.site.siteMetadata.url}${imageUrl}`} />
          )}
          <link rel="canonical" href={`${data.site.siteMetadata.url}${pathname}`} />
          <link
            rel="preload"
            href="https://files.raymasoft.com/Yekan.ttf"
            as="font"
            type="font/ttf"
            crossOrigin="anonymous"
          />
          <html lang="fa" />
          {faqs && <script type="application/ld+json">{script}</script>}
        </Helmet>
      )}
    />
  );
};

export default SEO;
