import React from "react";
import siteLogo from "../Images/site_logo.png";
import { StaticQuery, graphql, Link } from "gatsby";

const SocialLinks = ({ links }) => {
  return (
    <StaticQuery
      query={graphql`
        {
          allFile(filter: { sourceInstanceName: { eq: "src/Images" } }) {
            nodes {
              name
              extension
              childImageSharp {
                fixed {
                  src
                }
              }
            }
          }
        }
      `}
      render={(data) => (
        <div
          className="flex-col p-5"
          data-aos="fade-up"
          data-aos-offset="150"
          data-aos-easing="ease"
          data-aos-duration="600"
        >
          <img
            loading={"lazy"}
            src={siteLogo}
            alt="site-logo"
            width="120"
            height="120"
            className="m-auto lg:my-4"
          />
          <p className="text-xs text-center ">ما را در شبکه های اجتماعی دنبال کنید:</p>
          <div className="flex mx-auto justify-center">
            {links.map((link) => (
              <a
                key={link.name}
                rel="noopener"
                target="_blank"
                href={link.url}
                className="m-2 hover:opacity-75"
              >
                <img
                  loading={"lazy"}
                  alt={link.name}
                  width="48"
                  height="48"
                  src={
                    data.allFile.nodes.find((image) => image.name === link.name).childImageSharp
                      .fixed.src
                  }
                />
              </a>
            ))}
          </div>
          <p className="text-xs my-4 text-center">
            تمامی حقوق محفوظ و متعلق به شرکت رایورز می باشد.
          </p>
        </div>
      )}
    />
  );
};
export default SocialLinks;
