import React from "react";
import { slide as Menu } from "react-burger-menu";
import "../Styles/BurgerMenu.css";
import { Link } from "gatsby";
import logo from "../Images/site_logo.png";
import avatar from "../Images/Avatar.svg";
import {ChevronDown, ChevronUp}  from "./Icons";


const BurgerMenu = (props) => {

  const [isActive, setIsActive] = React.useState(false)
  const [display, setDisplay] = React.useState("hidden")
  const hideShowDiv = (e) => {
    const oldValue = isActive
    setIsActive(!isActive)
    if (!oldValue)
      setDisplay("flex flex-col")
    else
      setDisplay("hidden")
  }

  return (
    <div className="fixed bg-white w-full visible lg:invisible z-10">
      <Menu right disableCloseOnEsc>
        {props.links.map((link) => (
          link.subMenu ? <ul className="flex-col">
            <div className="flex">
            <li onClick={hideShowDiv} className={`${isActive && "text-teal-500"}`}>{link.name} </li>
            {isActive ? <ChevronDown /> : <ChevronUp />}
            </div>
            <ul className={`pt-4 ${display} whitespace-no-wrap`}>
              {link.subMenu.map(sub => (
                <Link className="pr-8 pt-4" id={sub.name} key={sub.name} to={sub.link}>
                  {sub.name}
                </Link>
              ))}
            </ul>
          </ul> :
            <Link className="" id={link.name} key={link.name} to={link.link}>
              {link.name}
            </Link>
        ))}
        {!props.userIsLogin ? (
          <li onClick={props.loginFunc}>{props.login.name}</li>
        ) : (
          <li onClick={props.logout}>{props.login.logoutName}</li>
        )}
      </Menu>
      <div className="flex my-6 mx-4 items-center">
        <Link to="/">
          <img src={logo} alt="site-logo" width="70" height="70" />
        </Link>
        {props.userIsLogin && (
          <Link to="/profile/">
            <img src={avatar} alt="avatar" width="40" height="40" className="mx-3" />
          </Link>
        )}
      </div>
    </div>
  )
};

export default BurgerMenu;
