import React from "react";
import { StaticQuery, graphql } from "gatsby";
import Header from "./Header";
import Footer from "./Footer";
import "aos/dist/aos.css";
import AOS from "aos";

const Layout = ({ page, children, faqs }) => {
  const faqList = faqs
    ? faqs.map(function (faq) {
        const items = Object.keys(faq).map((key) => {
          return faq[key];
        });
        return items;
      })
    : null;

  if (
    typeof document !== "undefined" &&
    typeof window !== "undefined" &&
    window.location.host !== "localhost:5339"
  )
    AOS.init();
  return (
    <StaticQuery
      query={graphql`
        query SiteTitleQuery {
          site {
            siteMetadata {
              menuLinks {
                name
                showInHeader
                link 
                subMenu{
                  name
                  link
                }
              }
              socialLinks {
                name
                url
              }
              contacts {
                name
                value
              }
              login {
                url
                name
                logoutName
              }
            }
          }
        }
      `}
      render={(data) => (
        <div id="layout" className={"overflow-hidden page-" + page} style={{ direction: "rtl" }}>
          <Header
            menuLinks={data.site.siteMetadata.menuLinks.filter((link) => link.showInHeader)}
            login={data.site.siteMetadata.login}
          />
          <div className="my-24 mx-auto">{children}</div>
          <div className="w-10/12 lg:w-4/5 my-6 mx-auto mb-12">
            {faqList &&
              faqList.map((faq) => (
                <details className="border border-gray-300">
                  <summary className="bg-inherit px-5 py-3 text-lg cursor-pointer">
                    {faq[0]}؟
                  </summary>
                  <div className="bg-white px-5 py-3 border border-gray-300 text-sm font-light">
                    <p>{faq[1]}</p>
                  </div>
                </details>
              ))}
          </div>
          <Footer
            contacts={data.site.siteMetadata.contacts}
            menuLinks={data.site.siteMetadata.menuLinks}
            socialLinks={data.site.siteMetadata.socialLinks}
          />
        </div>
      )}
    />
  );
};

export default Layout;
