import React from "react";
import { Link } from "gatsby";
import BurgerMenu from "./BurgerMenu";
import logo from "../Images/site_logo.png";
import avatar from "../Images/Avatar.svg";
import { Menu } from '@mantine/core';
import {ChevronUp}  from "./Icons";

const Header = ({ menuLinks, login }) => {
  function GetCookie(name) {
    if (typeof document === "undefined") return null;
    const value = `; ${document.cookie}`;
    const parts = value.split(`; ${name}=`);
    if (parts.length === 2) return parts.pop().split(";").shift();
    return null;
  }

  function Logout() {
    window.Logout(login.url);
    setTimeout(function () {
      window.location = "/";
    }, 3000);
  }

  function Login() {
    window.location = login.url + "?returnUrl=" + window.location;
  }

  let cookie = GetCookie("Claims");
  let userIsLogin = cookie !== null;

  return (
    <header className="w-screen">
      <BurgerMenu
        links={menuLinks}
        avatar={avatar}
        login={login}
        loginFunc={Login}
        logout={Logout}
        userIsLogin={userIsLogin}
      />
      <div className="invisible lg:visible fixed w-full bg-white z-10  shadow-lg">
        <div>
          <div>
            <nav>
              <ul className="flex py-4 items-center justify-center">
                <Link to="/" className="hover:opacity-75">
                  <img src={logo} alt="site-logo" width="70" height="70" className="mx-2" />
                </Link>
                {menuLinks.map((link) => (
                  link.subMenu ? 
                  <Menu shadow="lg"  withArrow trigger="hover" openDelay={100} closeDelay={400}>
                    <Menu.Target style={{display:"flex", fontFamily:"inherit"}}>
                     <li key={link.name}  className=" list-none m-4 text-base text-teal-600 whitespace-no-wrap font-bold hover:text-green-400">
                      {link.name}
                      <ChevronUp color={"#319795"} width={10} marginRight={5} />
                     </li>
                    </Menu.Target>
                    <Menu.Dropdown style={{borderRight: "2px solid green"}} >
                      {link.subMenu.map(sub => (
                        <Menu.Item style={{textAlign:"right"}} >
                          <Link to={sub.link} >
                          {sub.name}
                          </Link>  
                        </Menu.Item>
                      ))}
                      </Menu.Dropdown>
                  </Menu>
                  : 
                  <li className="list-none m-4" key={link.name}>
                    <Link
                      className="text-base text-teal-600 whitespace-no-wrap font-bold hover:text-green-400"
                      to={link.link}
                      activeClassName="active-link"
                    >
                      {link.name}
                    </Link>
                  </li>
                ))}
                {userIsLogin ? (
                  <li className="list-none m-4 whitespace-no-wrap">
                    <button
                      className="mx-2 w-20 pb-1 h-8 bg-teal-600 hover:bg-green-600 text-white font-bold rounded-full text-center"
                      onClick={Logout}
                    >
                      {login.logoutName}
                    </button>

                    <Link to="/profile/">
                      <img
                        src={avatar}
                        alt="avatar"
                        width="40"
                        height="40"
                        className="inline mr-2"
                      />
                    </Link>
                  </li>
                ) : (
                  <li className="list-none m-4">
                    <button
                      className="mx-2 w-20 pb-1 h-8 bg-teal-600 hover:bg-green-600 text-white font-bold rounded-full text-center "
                      onClick={Login}
                    >
                      {login.name}
                    </button>
                  </li>
                )}
              </ul>
            </nav>
          </div>
        </div>
      </div>
    </header>
  );
};
export default Header;
